	module.exports = {"data":{"markdownRemark":{"html":"<h2>Lang</h2>\n<p>In HTML, Lang tag is used to declare the language of the whole or a part of a web page.</p>\n<h3>Examples</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span> <span class=\"token attr-name\">lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>en<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>The lang attribute can also be used to specify the language of a specific element:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span> <span class=\"token attr-name\">lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>hi<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  फ्री कोड कैंप\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>In the above example, “hi” denotes the Hindi language. Similarly, you can use “en” for English, “es” for Spanish, “fr” for French and so on.</p>\n<p>Refer to <a href=\"https://en.wikipedia.org/wiki/List_of_ISO_639-1_codes\">ISO 639-1</a> for the appropriate two-digit Language code.</p>","fields":{"slug":"/html/attributes/lang/"},"frontmatter":{"title":"Lang"}}},"pathContext":{"slug":"/html/attributes/lang/"}}

